import Router from 'vue-router'

Vue.use(Router)

let router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      meta: {
        keepAlive: true,
        title: '首页'
      },
      component: (resolve) => {
        import('@/components/home.vue').then((module) => {
          resolve(module)
        })
      }
    },
    {
      path: '/toilet',
      name: 'toilet',
      meta: {
        title: '公厕'
      },
      component: (resolve) => {
        import('@/components/Toilet/Index.vue').then((module) => {
          resolve(module)
        })
      }
    },
    {
      path: '/subway',
      name: 'subway',
      meta: {
        keepAlive: true,
        title: '地铁'
      },
      component: (resolve) => {
        import('@/components/SubWay/Index.vue').then((module) => {
          resolve(module)
        })
      }
    },
    {
      path: '/subway/ret',
      name: 'subwayRet',
      meta: {
        title: '规划结果'
      },
      component: (resolve) => {
        import('@/components/SubWay/Result.vue').then((module) => {
          resolve(module)
        })
      }
    }
  ]
})

export default router



